
html, body {
  width: 100%;
  min-height: 100%;
  background-color: #ebebeb;
}
footer {
  background: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 1.30667rem;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-shadow: 0 0 8px 4px rgba(240, 240, 240, 0.7);
          box-shadow: 0 0 8px 4px rgba(240, 240, 240, 0.7);
  z-index: 1;
}
footer div {
    height: 100%;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
footer div .icons {
      display: block;
      width: 0.66667rem;
      height: 0.58667rem;
      margin-top: 0.16rem;
      margin-bottom: 0.05333rem;
}
footer div .home {
      background: url("./assets/images/f1.png");
      background-size: 100% 100%;
}
footer div .classify {
      background: url("./assets/images/f3.png");
      background-size: 100% 100%;
}
footer div .discover {
      background: url("./assets/images/f5.png");
      background-size: 100% 100%;
}
footer div .shopcar {
      background: url("./assets/images/f7.png");
      background-size: 100% 100%;
}
footer div .person {
      background: url("./assets/images/f9.png");
      background-size: 100% 100%;
}
footer div span {
      font-size: 10px;
      line-height: 1.1;
      color: #6f6f6f;
}
[data-dpr="2"] footer div span {
        font-size: 20px;
}
[data-dpr="3"] footer div span {
        font-size: 30px;
}
footer .router-link-exact-active .home {
    background: url("./assets/images/f2.png");
    background-size: 100% 100%;
}
footer .router-link-exact-active .classify {
    background: url("./assets/images/f4.png");
    background-size: 100% 100%;
}
footer .router-link-exact-active .discover {
    background: url("./assets/images/f6.png");
    background-size: 100% 100%;
}
footer .router-link-exact-active .shopcar {
    background: url("./assets/images/f8.png");
    background-size: 100% 100%;
}
footer .router-link-exact-active .person {
    background: url("./assets/images/f10.png");
    background-size: 100% 100%;
}
footer .router-link-exact-active span {
    color: #ff3737;
}
.empty {
  width: 100%;
  height: 1.30667rem;
}



